import React, { Component } from 'react'
import { BrowserRouter, Switch, Route } from 'react-router-dom'

import Home from '../components/home/Home'
import Search from '../components/search/Search'
import Result from '../components/resut/Result'
import Characters from '../components/characters/Characters'
import Comics from '../components/comics/Comics'

class App extends Component {
  render() {
    return(
      <BrowserRouter>
        <Switch>
          <Route path="/marvel-app/" exact component={Home} />
          <Route path="/marvel-app/search" component={Search} />
          <Route path="/marvel-app/result" component={Result} />
          <Route path="/marvel-app/characters" component={Characters} />
          <Route path="/marvel-app/comics" component={Comics} />
        </Switch>
      </BrowserRouter>
    )
  }
}

export default App
